'use strict';

/**
* Zip one or more arrays.
*
* @module @stdlib/array-base-zip
*
* @example
* var zip = require( '@stdlib/array-base-zip' );
*
* var x = [ 1, 2, 3 ];
* var y = [ 'a', 'b', 'c' ];
*
* var z = zip( [ x, y ] );
* // returns [ [ 1, 'a' ], [ 2, 'b' ], [ 3, 'c' ] ]
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
